.drum-pad{
    width: 50px;
    height: 25px;
    background-color: black;
    margin: 3px;
    color: white;
 
    border: none;
    position:relative;
    display: inline-block;
    left: -30px;
    top: -30px;
}


.drum-pad:hover{
background-color: yellow;
cursor: pointer;
}
#text{
    position: absolute;
    left: 10px;
}


#names{
    position: absolute;
    top: 15px;
    right: 10px;
    left: 10px;
    color: black;
    text-align: right;
    font-size: 6x;
    margin: 10px;
 
}

#namesBack{
    position: absolute;
    top: 20px;
    right: 10px;
    left: 190px;
    width: 100px;
    height: 10px;
    background-color: lightblue;
    margin: 5px auto;
    padding: 15px;
    box-sizing: border-box;
}



